import http from '../../common/http.js'
export default {
	data() {
		return {
			imgUrl: http.imgUrl,
			address_id: '',
			show: false,
			isChoose: 0,
			regionText: '请选择',
			regionObj: {},
			params: {
				province: true,
				city: true,
				area: true
			}
		}
	},
	onLoad: function(e) {
		console.log(e)
		if (e.address_id !== 'false') {
			uni.setNavigationBarTitle({
				title: '编辑地址'
			});
			this.address_id = e.address_id
			this.allRequest()
		}

	},
	onShow: function() {

	},
	onPullDownRefresh: function() {

		// uni.stopPullDownRefresh(); //停止下拉刷新动画
	},
	onReachBottom: function() {

	},
	methods: {
		allRequest: function() {
			this.getReceive()
		},
		getReceive: function() {
			uni.showLoading({
				title: '加载中'
			})
			let opts = {
				url: 'Members/getReceive',
				method: 'post'
			};
			let param = {
				user_id: '1829',
				receive_id: this.address_id,
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				uni.hideLoading()
			}, error => {
				console.log(error);
				uni.hideLoading()
			})
		},
		showRegionModal: function() {
			this.show = 1
		},
		chooseRegion: function(e) {
			this.regionText = e.province.label + '-' + e.city.label + '-' + e.area.label
			this.isChoose = 1
			this.regionObj = e
			console.log(e)
		}
	}
}
